	module.exports = {"data":{"site":{"siteMetadata":{"title":"Return of the Shadow"}},"markdownRemark":{"id":"/home/kvalencik/git/rots-site/src/pages/news/001596.md absPath of file >>> MarkdownRemark","html":"<blockquote>\n<p>Message 1596 : Wed Dec 23 (Blent)      :: Tweaks to Armor</p>\n</blockquote>\n<p>A few pieces of armor have been tweaked.</p>\n<p>Specifically, the mithril armor from the cold-drake has been standardized,\nthe ancient mithril breastplate has been completely remodeled (and re-named),\nand the light silver helmet has been renamed (but not remodeled).</p>\n<p>Additionally, etched leather is now marginally heavier than before, and\nprovides slightly worse protection.</p>\n<p>It also came to our attention that the intended balancing malus on the\nlong bone sceptre was not functioning properly. This has been fixed.</p>\n<p>Send any complaints to Blent.</p>","frontmatter":{"title":"News 1596: Tweaks to Armor","date":"October 09, 2017"}}},"pathContext":{"path":"/news/1596"}}

